<template>
  <v-container fluid>
    <canvas id="matrix"></canvas>
    <v-row>
      <v-col cols="12" align="center" class="name">George Kampitakis</v-col>
    </v-row>
    <v-row>
      <v-col cols="12" align="center" class="title"
        >Software Engineer | Fullstack Developer</v-col
      >
    </v-row>
    <v-row v-if="loading">
      <v-col cols="12" align="center">
        <v-progress-circular :size="70" class="loader" indeterminate />
      </v-col>
    </v-row>
    <v-row v-else-if="!loading && !error">
      <v-col cols="12" align="center">
        <media-buttons></media-buttons>
      </v-col>
    </v-row>
    <v-row v-else>
      <v-col cols="12" align="center">
        <errorHandler />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import mediaButtons from "./media-buttons";
import errorHandler from "./errors/errorHandler";
import { mapGetters } from "vuex";

export default {
  name: "home",
  components: {
    mediaButtons,
    errorHandler
  },
  computed: {
    ...mapGetters(["jsonData", "loading", "error"])
  },
  mounted() {
    this.setBackGround();
  },
  methods: {
    setBackGround: () => {
      const canvas = document.getElementById("matrix"),
        canvasContext = canvas.getContext("2d"),
        characters = "qwertyuiopasdfghjkl1234567890zxcvbnm",
        font_size = 10,
        drops = [];

      canvas.height = window.innerHeight;
      canvas.width = window.innerWidth;

      const columns = canvas.width / font_size;

      for (let x = 0; x < columns; x++) drops[x] = 1;

      function draw() {
        canvasContext.fillStyle = "rgba(0, 0, 0, 0.05)";
        canvasContext.fillRect(0, 0, canvas.width, canvas.height);

        canvasContext.fillStyle = "#0F0";
        canvasContext.font = font_size + "px arial";
        //looping over drops
        for (let i = 0; i < drops.length; i++) {
          const text =
            characters[Math.floor(Math.random() * characters.length)];

          canvasContext.fillText(text, i * font_size, drops[i] * font_size);

          if (drops[i] * font_size > canvas.height && Math.random() > 0.975)
            drops[i] = 0;

          //incrementing Y coordinate
          drops[i]++;
        }
      }

      setInterval(draw, 33);
    }
  }
};
</script>

